html,
body {
  height: 100%;
}

.navbar {
  margin-bottom: 0;
}

#block-player {
  width: 100%;
  height: 100%;
  margin-top: -52px;
  padding-top: 52px;
}

#block-player #player-youtube {
  float: left;
  width: 70%;
  height: 100%;
}

#block-player #chat {
  position: relative;
  float: left;
  width: 30%;
  min-height: 100%;
}

#block-player #chat iframe {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.embed-container {
  position: relative;
  overflow: hidden;
  max-width: 100%;
  height: 0;
  padding-bottom: 56.25%;
}

.embed-container iframe,
.embed-container object,
.embed-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

